<template>
    <div class='chose-sprinte'>
        <div class='chose'>
            <div class='chose_inner'>
                <img @click='choseSprinte(0)' class='sprint_size thunder positions' src="@/assets/img/home/guide/srpinte_thunder.png" alt="">
                <img v-if='sprinteChoseIndex==0' class='sprinte_bg positions' src="@/assets/img/home/guide/chose_background.png" alt="">
                <img class='arrows_size positions arrows_right' src="@/assets/img/home/guide/arrows_right.png" alt="">
                <img @click='choseSprinte(1)' class='sprint_size positions ice' src="@/assets/img/home/guide/srpinte_ice.png" alt="">
                <img  v-if='sprinteChoseIndex==1' class='positions sprinte_bg icebg' src="@/assets/img/home/guide/chose_background.png" alt="">
           
                <img class='positions arrows_grow arrows_upp' src="@/assets/img/home/guide/arrows_up.png" alt="">
                <img class='positions arrows_grow arrows_downp' src="@/assets/img/home/guide/arrows_down.png" alt="">
            
                <img @click='choseSprinte(2)' class='sprint_size positions wood' src="@/assets/img/home/guide/srpinte_wood.png" alt="">
                <img  v-if='sprinteChoseIndex==2' class='positions sprinte_bg woodbg' src="@/assets/img/home/guide/chose_background.png" alt="">
                <img class='positions arrows_size arrows_left' src="@/assets/img/home/guide/arrows_left.png" alt="">
                <img @click='choseSprinte(3)' class='sprint_size positions fire' src="@/assets/img/home/guide/srpinte_fire.png" alt="">
                <img  v-if='sprinteChoseIndex==3'  class='positions sprinte_bg firebg' src="@/assets/img/home/guide/chose_background.png" alt="">
            </div>
        </div>
        <div class='text'>
            每个种族的精灵们都存在这相互克制的关系, 因此如何培养精灵成为了召唤师们重要的课题， 请选择一只精灵作为你的初始伙伴吧！
        </div>
        <div class='chose_show'>
            <div class='tag'>雷族 <p class='left'></p><p class='right'></p></div>
            <div class='detail'>
                强大的种族，可以以闪电般的破坏力与 速度进攻敌人。
            </div>
        </div>
        <div class='input_name'>
            <p>告诉我们您的名字吧！</p>
            <input :maxlength="5" type="text" v-model.trim="sendForm.name">
        </div>
        <div class='submit_button'><img @click='submit' src="@/assets/img/home/guide/button_sure.png" alt=""></div>
    </div>
</template>
<script>
export default {
    name:'choseSprinte',
    data(){
        return{
            sprinteChoseIndex:0,
            sendForm:{
                name:null,
            },
        }
    },
    methods:{
        choseSprinte(index){
            this.sprinteChoseIndex = index;
        },
        submit(){
            if(this.sendForm.name == null || this.sendForm.name == ''){
                this.$Toast('请输入您的名字！')
            }else{
                this.$router.push({path:'/main'})
            }
        },
    }

}
</script>
<style lang="less" scope>
@import '../../../common/css/pulic.less';
    .chose-sprinte{
        width:100%;
        height:100%;
        padding:30px 71px;
        overflow-y: auto;
        .box-sizing(@border-box);
        .layer-background(@backgroundColor);
        .chose{
            width:544px;
            height:565px;
            margin: 0 auto;
            overflow: hidden;
            .chose_inner{
                width:100%;
                height:100%;
                margin:0 auto;
                position:relative;
                .sprint_size{
                    width:141px;
                    height:171px;
                }
                .sprinte_bg{
                    width:218px;
                    height:206px;
                }
                .arrows_size{
                    width:180px;
                    height:45px;
                }
                .arrows_grow{
                    width:45px;
                    height:175px;
                }
                .positions{
                    position:absolute;
                }
                .thunder{
                    left:40px;
                    top:8px;
                    z-index:103;
                }
                .arrows_right{
                    top:86.5px;
                    left:186px;
                     z-index:102;
                }
                .ice{
                    top:12px;
                    left:372px;
                    z-index:103
                }
                .icebg{
                    top:8px;
                    left:328px;
                }
                .arrows_upp{
                    top:181px;
                    left:88px;
                }
                .arrows_downp{
                    top:196px;
                    right:88px;
                }
                .wood{
                    left:40px;
                    top:358px;
                    z-index:103;
                }
                .woodbg{
                    top:342px;
                    //left:186px;
                    z-index:102;
                }
                .arrows_left{
                    top:442px;
                    left:186px;
                     z-index:102;
                }
                .fire{
                      top:358px;
                    left:372px;
                    z-index:103
                }
                .firebg{
                     top:342px;
                    left:328px;
                }
            }
        }
        .text{
            width:576px;
            height:auto;
            color:white;
            font-size:26px;
            line-height: 46px;
            margin:0 auto  34px auto;
        }
        .chose_show{
            width:100%;
            height:auto;
            .tag{
                width:74px;
                height:62px;
                line-height: 62px;
                color:#f6f303;
                font-size:25px;
                background:#112f38;
                position:relative;
                text-align: center;
                margin-bottom:7px;
                .right{
                    height:0;
                    width:0;
                    position:absolute;
                    right:-62px;
                    top:0px;
                    border:31px solid #112f38;
                    border-color:transparent  transparent transparent #112f38;
                }
                .left{
                    position:absolute;
                    left:-62px;
                    top:0px;
                    height:0;
                    width:0;
                    border:31px solid #112f38;
                    border-color:transparent #112f38 transparent transparent;

                }
            }
            .detail{
                width:576px;
                height:auto;
                color:white; 
                font-size:26px;
                line-height: 46px;
                margin:0 auto  30px auto;
            }
        }
        .input_name{
             width:576px;
             height:auto;
             margin:0 auto 49px auto;
             p{
                 font-size:26px;
                 color:#f6f303;
                 margin-bottom:15px;
             }
             input{
                 display:block;
                 width:100%;
                 height:74px;
                 background:#0f3039;
                 color:white;
                 -moz-box-shadow:0px 5px 5px #040c0f inset;               /* For Firefox3.6+ */
                    -webkit-box-shadow:0px 5px 5px  #040c0f inset;            /* For Chrome5+, Safari5+ */
                    box-shadow:0px 5px 5px  #040c0f inset;                    /* For Latest Opera */
             }
        }
        .submit_button{
            width:100%;
            height:auto;
            img{
                display:block;
                width:192px;
                height:73px;
                margin:0 auto;
            }
        }
    }
</style>



